
.review {
    .container {
        max-width: 1350px;
    }
}

.reviews__title {
    margin-bottom: 64px;
}

.review__list {
    padding: 0 80px;
    position: relative;
    @include sm-block {
        padding: 0;
    }
}

.review__slider {
    position: relative;
    overflow: hidden;
    padding-bottom: 40px;
}


.review__item {
    font-size: 16px;
    line-height: 1.5;
}

.review__item-text {
    background: rgba(#D9D9D9, .1);
    color: $color-text;
    border-radius: 12px;
    margin-bottom: 31px;
    padding: 24px 32px;
    min-height: 245px;  
    position: relative;
    &::before {
        position: absolute;
        bottom: -20px;
        width: 28px;
        height: 20px;
        left: 20px;
        content: '';
        opacity: .1;
        background: url('../images/ico-polygon.png') top left / contain no-repeat;
    }
}

.review__item-bottom {
    display: grid;
    grid-template-columns: 64px 1fr;
    gap: 16px;
}

.review__item-name {
    margin-bottom: 2px;
}

.review__item-post {
    color: $color-text;
}