.header {
    width: 100%;
    padding: 24px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9;
    &--secondary {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
    .container {
        max-width: 1370px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    @include md-block {
        border-bottom: none;
        .container {
            padding: 0 20px;
        }
    }
    @include sm-block {
        .container {
            padding: 0 15px;
        }
    }
} 
.header-nav {
    max-width: 703px;
    width: 100%;
    display: flex;
    align-items: center; 
    @include lg-block {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100vh;
        overflow-y: auto;
        max-width: 100%;
        width: 100%;
        background-color: #151427;
        z-index: 10;
        padding: 0 20px 160px;
        display: none;
        &.active {
            display: block;
        }
    }
    @include sm-block {
        padding: 0 15px;
    }
}

.header-nav__head {
    display: none;
    @include lg-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 24px 0px;
        margin-bottom: 42px;
    }
}

.header-nav__btn {
    display: none;
    @include lg-block {
        display: block;
        max-width: 260px;
        width: 100%;
        text-align: center;
        margin: 40px auto 0;
    }
}
.header-nav__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 600px;
    width: 100%;
    @include lg-block {
        flex-direction: column;
        margin: 0 auto;
    }
}

.header-nav__item {
    font-size: 16px;
    line-height: 24px;
    a {
        color: $color-text;
        &:hover {
            color: $color-primary;
        }
    }
    @include lg-block {
        padding: 40px 0;
        font-size: 40px;
        line-height: 1.4;
        @include font-main();
        font-weight: 400;
        position: relative;
        width: 100%;
        text-align: center;
        &::before {
            display: block;
            position: absolute;
            content: "";
            bottom: 10px;
            width: 100%;
            height: 2px;
            background: rgb(255, 255, 255);
            background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(50%, rgba(255, 255, 255, 0.2077205882)), to(rgba(255, 255, 255, 0)));
            background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.2077205882) 50%, rgba(255, 255, 255, 0) 100%);
            z-index: 2;
        }
    }
    @include sm-block {
        font-size: 24px;
        padding: 24px 0;
    }
}

.header__lang {
    margin-left: 60px;
    font-size: 16px;
    line-height: 1.4;
    text-transform: uppercase;
    color: $color-text;
    @include lg-block {
        max-width: 105px;
        margin: 40px auto 0;
        text-transform: initial;
    }
}

.header__btn {
    @include lg-block {
        margin: 0 76px 0 auto;
    }
    @include sm-block {
        display: none;
    }
}

.header__logo {
    @include sm-block {
        height: 48px;
        img {
            width: auto;
            height: 100%;
        }
    }
}


.header__burger  {
    display: none;
    @include lg-block {
        display: block;
        width:  30px;
        height: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: $color-white;
        }
    }
}