.products {
    .container {
        max-width: 1350px;
    }
}

.products__slider {
    overflow: hidden;
    position: relative;
    padding-bottom: 44px;
}

.products__list {
    padding: 0 80px;
    position: relative;
    @include sm-block {
        padding: 0 40px;
    }
}

.products__item {
    padding: 32px;
    background: linear-gradient(137.71deg, rgba(77, 43, 146, 0.2) -7.29%, rgba(146, 98, 241, 0.05) 109.93%);
    border-radius: 10px;
    font-size: 16px;
    line-height: 1.5;
}

.products__item-logo {
    margin-bottom: 24px;
}

.products__item-company {
    margin-bottom: 24px;
    span {
        color: $color-text;
        display: block;
        margin-bottom: 5px;
    }
}

.products__item-geo {
    margin-bottom: 24px;
}

.products__item-geo-subtitle {
    margin-bottom: 15px;
    color: $color-text;
}

.products__item-countries {
    display: flex;
    align-items: center;
    gap: 16px;
}

.products__item-price {
    span {
        display: block;
        color: $color-text;
        margin-bottom: 7px;
    }
}