.affiliates {
    .page-head {
        background: url('../images/affiliates-bg.png') center center / cover no-repeat;
    }
}

.affiliates__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 90px;
    padding: 0 30px 0;
    @include md-block {
        max-width: 472px;
        margin: 0 auto;
        grid-template-columns: 1fr;
        gap: 64px;
    }
    @include sm-block {
        gap: 40px;
    }
}